import React, {
	FC, ReactNode,
} from "react";

import { spacingMap } from "../../styleguide";

import {
	LocalNavHeader,
	LocalNavigationItemsWrapper,
	LocalNavigationWrapper,
} from "./LocalNavigation.styles";

export interface LocalNavigationProps {
	dataQA: string
	ariaLabel: string
	className?: string
	children?: React.ReactNode
	header?: ReactNode
}

const LocalNavigation: FC<LocalNavigationProps> = ({
	dataQA,
	ariaLabel,
	children,
	className,
	header,
}) => (
	<LocalNavigationWrapper
		aria-label={ariaLabel}
		data-qa={dataQA}
		className={className}
	>
		{header &&
			<LocalNavHeader
				dataQA="local-navigation-header"
				variant="displaySmall"
				component="h2"
				m={`0 0 0 ${spacingMap.xs}`}
			>
				{header}
			</LocalNavHeader>}
		<LocalNavigationItemsWrapper>
			{children}
		</LocalNavigationItemsWrapper>
	</LocalNavigationWrapper>
);

LocalNavigation.displayName = "LocalNavigation";
export default LocalNavigation;
